import React, { Component } from "react";
import Web3 from "web3";
import detectEthereumProvider from '@metamask/detect-provider';

import Home from "../V1Home";
import TronLinkGuide from "../TronLinkGuide";
import cons from "../../cons"

import abiToken from "../../token";
import abiBinario from "../../binary";

const Cifret = process.env.APP_ENCR_STO || "errated";

console.log(Cifret)

var addressToken = cons.TOKEN;
var addressBinary = cons.SC;
var chainId = cons.chainId;

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      admin: false,
      metamask: false,
      conectado: false,
      currentAccount: "0x0000000000000000000000000000000000000000",
      binanceM:{
        web3: null,
        contractToken: null,
        contractBinary: null
      }
      
    };
  }

  async componentDidMount() {

    this.conectar();

    setInterval(() => {
      this.conectar();
    }, 3*1000);

  }

  async conectar(){


        if (typeof window.ethereum !== 'undefined') {  
          
          this.setState({
            metamask: true
          })

          await window.ethereum.request({
            method: 'wallet_switchEthereumChain',
            params: [{ chainId: chainId}],
          });


          window.ethereum.request({ method: 'eth_requestAccounts' })
          .then(async(accounts) => {

            const provider = await detectEthereumProvider();
            
            var web3 = new Web3(provider);
            var contractToken = new web3.eth.Contract(
              abiToken,
              addressToken
            );
            var contractBinary = new web3.eth.Contract(
              abiBinario,
              addressBinary
            );

            var isAdmin = false;

            if(await contractBinary.methods.admin(accounts[0]).call({from:accounts[0]})){
              isAdmin = "admin"
            }

            if(await contractBinary.methods.leader(accounts[0]).call({from:accounts[0]})){
              isAdmin = "leader"
            }

            if((await contractBinary.methods.owner().call({from:accounts[0]})).toLowerCase() === accounts[0]){
              isAdmin = "owner"
            }

            var verWallet = accounts[0];
            var loc = document.location.href;

            if(loc.indexOf('?')>0 && loc.indexOf('view')>0){
                      
              verWallet = loc.split('?')[1];
              verWallet = verWallet.split('=')[1];
              verWallet = verWallet.split('#')[0];

              if(!web3.utils.isAddress(verWallet)){
                verWallet = await contractBinary.methods.idToAddress(verWallet).call({from:accounts[0]});
              }
        
            }

            this.setState({
              conectado: true,
              currentAccount: verWallet,
              admin: isAdmin,
              binanceM:{
                web3: web3,
                contractToken: contractToken,
                contractBinary: contractBinary
              }
            })
            
          })
          .catch((error) => {
            console.error(error)
            this.setState({
              conectado: false,
              admin: false,
              binanceM:{
                web3: null,
                contractToken: null,
                contractBinary: null
              }
            })   
          });
  
        } else {    
          this.setState({

            metamask: false,
            conectado: false,
            admin: false,
            binanceM:{
              web3: null,
              contractToken: null,
              contractBinary: null
            }
          })        
             
        }

      }




  render() {

    var ruta = "";
    var loc = document.location.href;
    //console.log(loc);
    if(loc.indexOf('?')>0){
              
      ruta = loc.split('?')[1];
      ruta = ruta.split('=')[0];
      ruta = ruta.split('#')[0];

    }

    if (!this.state.metamask) return (
      <>
        <div className="container">
          <TronLinkGuide />
        </div>
      </>
      );

    if (!this.state.conectado) return (
      <>
        <div className="container">
          <TronLinkGuide installed />
        </div>
      </>
      );

    if(this.state.admin){
      var admin = true;
    }

    switch (ruta) {
      case "view":
        return(<Home admin={admin} contractAddress={this.state.binanceM.contractBinary._address} version="1" wallet={this.state.binanceM} currentAccount={this.state.currentAccount}/>);
      case "shasta":
      case "test":
      case "v0":
      case "V0": 
      case "prueba": 
        return(<Home admin={this.state.admin} contractAddress={cons.SCtest} version="999" wallet={this.state.binanceM} currentAccount={this.state.currentAccount}/>);
      default:
        return(<Home admin={this.state.admin} contractAddress={this.state.binanceM.contractBinary._address} version="1" wallet={this.state.binanceM} currentAccount={this.state.currentAccount}/>);
    }


  }
}
export default App;

